import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useQuery, useMutation } from '@tanstack/react-query';
import { api } from '../lib/client';
import { useAuthStore } from '../stores/authStore';

export function Settings() {
  const navigate = useNavigate();
  const { logout } = useAuthStore();
  const [showDeleteDialog, setShowDeleteDialog] = useState(false);
  const [deleteConfirmation, setDeleteConfirmation] = useState('');
  const [exportStatus, setExportStatus] = useState<string | null>(null);

  // Fetch user profile
  const { data: profile, isLoading: isProfileLoading } = useQuery({
    queryKey: ['user', 'profile'],
    queryFn: async () => {
      const res = await api.user.profile.$get();
      if (!res.ok) {
        throw new Error('Failed to fetch profile');
      }
      return res.json();
    },
  });

  // Fetch user stats
  const { data: stats } = useQuery({
    queryKey: ['user', 'stats'],
    queryFn: async () => {
      const res = await api.user.stats.$get();
      if (!res.ok) {
        throw new Error('Failed to fetch stats');
      }
      return res.json();
    },
  });

  // Delete account mutation
  const deleteMutation = useMutation({
    mutationFn: async () => {
      const res = await api.user.account.$delete();
      if (!res.ok) {
        throw new Error('Failed to delete account');
      }
      return res.json();
    },
    onSuccess: () => {
      logout();
      navigate('/');
    },
  });

  const handleExport = async (format: 'json' | 'csv') => {
    try {
      setExportStatus('...');

      const res = await api.user.export.$get({ query: { format } });

      if (!res.ok) {
        throw new Error('Export failed');
      }

      const blob = await res.blob();
      const contentDisposition = res.headers.get('Content-Disposition');
      const filename = contentDisposition?.match(/filename="(.+)"/)?.[1] || `health-data.${format}`;

      // Create download link
      const url = URL.createObjectURL(blob);
      const a = document.createElement('a');
      a.href = url;
      a.download = filename;
      document.body.appendChild(a);
      a.click();
      document.body.removeChild(a);
      URL.revokeObjectURL(url);

      setExportStatus('');
      setTimeout(() => setExportStatus(null), 3000);
    } catch {
      setExportStatus('');
      setTimeout(() => setExportStatus(null), 3000);
    }
  };

  const handleDeleteAccount = () => {
    if (deleteConfirmation === '') {
      deleteMutation.mutate();
    }
  };

  if (isProfileLoading) {
    return (
      <div className="flex items-center justify-center py-12">
        <div className="h-8 w-8 animate-spin rounded-full border-4 border-blue-600 border-t-transparent" />
      </div>
    );
  }

  return (
    <div className="space-y-8">
      <div>
        <h1 className="text-2xl font-bold text-gray-900"></h1>
        <p className="mt-1 text-gray-600"></p>
      </div>

      {/* Profile Section */}
      <div className="rounded-lg border border-gray-200 bg-white p-6">
        <h2 className="mb-4 text-lg font-semibold text-gray-900"></h2>
        <div className="space-y-4">
          <div>
            <p className="text-sm text-gray-500"></p>
            <p className="font-medium text-gray-900">{profile?.email}</p>
          </div>
          {profile?.name && (
            <div>
              <p className="text-sm text-gray-500"></p>
              <p className="font-medium text-gray-900" data-testid="user-name">
                {profile.name}
              </p>
            </div>
          )}
          <div>
            <p className="text-sm text-gray-500"></p>
            <p className="font-medium text-gray-900">
              {profile?.createdAt
                ? new Date(profile.createdAt).toLocaleDateString('ja-JP')
                : '-'}
            </p>
          </div>
        </div>
      </div>

      {/* Stats Section */}
      {stats && (
        <div className="rounded-lg border border-gray-200 bg-white p-6">
          <h2 className="mb-4 text-lg font-semibold text-gray-900"></h2>
          <div className="grid grid-cols-2 gap-4 sm:grid-cols-4">
            <div className="text-center">
              <p className="text-2xl font-bold text-blue-600">{stats.weightRecords}</p>
              <p className="text-sm text-gray-500"></p>
            </div>
            <div className="text-center">
              <p className="text-2xl font-bold text-green-600">{stats.mealRecords}</p>
              <p className="text-sm text-gray-500"></p>
            </div>
            <div className="text-center">
              <p className="text-2xl font-bold text-orange-600">{stats.exerciseRecords}</p>
              <p className="text-sm text-gray-500"></p>
            </div>
            <div className="text-center">
              <p className="text-2xl font-bold text-gray-900">{stats.totalRecords}</p>
              <p className="text-sm text-gray-500"></p>
            </div>
          </div>
        </div>
      )}

      {/* Export Section */}
      <div className="rounded-lg border border-gray-200 bg-white p-6">
        <h2 className="mb-4 text-lg font-semibold text-gray-900"></h2>
        <p className="mb-4 text-sm text-gray-600">
          
        </p>
        <div className="flex flex-wrap gap-3">
          <button
            onClick={() => handleExport('json')}
            className="rounded-lg border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
          >
            JSON
          </button>
          <button
            onClick={() => handleExport('csv')}
            className="rounded-lg border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
          >
            CSV
          </button>
        </div>
        {exportStatus && (
          <p className="mt-3 text-sm text-gray-600">{exportStatus}</p>
        )}
      </div>

      {/* Delete Account Section */}
      <div className="rounded-lg border border-red-200 bg-red-50 p-6">
        <h2 className="mb-4 text-lg font-semibold text-red-900"></h2>
        <p className="mb-4 text-sm text-red-700">
          
        </p>
        <button
          onClick={() => setShowDeleteDialog(true)}
          className="rounded-lg bg-red-600 px-4 py-2 text-sm font-medium text-white hover:bg-red-700"
        >
          
        </button>
      </div>

      {/* Delete Confirmation Dialog */}
      {showDeleteDialog && (
        <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/50">
          <div className="mx-4 w-full max-w-md rounded-lg bg-white p-6 shadow-xl">
            <h3 className="text-lg font-semibold text-gray-900">
              
            </h3>
            <p className="mt-2 text-sm text-gray-600">
              {stats?.weightRecords || 0}
              {stats?.mealRecords || 0}{stats?.exerciseRecords || 0}
            </p>
            <div className="mt-4">
              <label className="block text-sm font-medium text-gray-700">
                
              </label>
              <input
                type="text"
                value={deleteConfirmation}
                onChange={(e) => setDeleteConfirmation(e.target.value)}
                placeholder=""
                className="mt-1 block w-full rounded-md border border-gray-300 px-3 py-2 shadow-sm focus:border-red-500 focus:outline-none focus:ring-1 focus:ring-red-500"
              />
            </div>
            <div className="mt-6 flex justify-end gap-3">
              <button
                onClick={() => {
                  setShowDeleteDialog(false);
                  setDeleteConfirmation('');
                }}
                className="rounded-lg border border-gray-300 bg-white px-4 py-2 text-sm font-medium text-gray-700 hover:bg-gray-50"
              >
                
              </button>
              <button
                onClick={handleDeleteAccount}
                disabled={deleteConfirmation !== '' || deleteMutation.isPending}
                className="rounded-lg bg-red-600 px-4 py-2 text-sm font-medium text-white hover:bg-red-700 disabled:opacity-50"
              >
                {deleteMutation.isPending ? '...' : ''}
              </button>
            </div>
            {deleteMutation.isError && (
              <p className="mt-3 text-sm text-red-600">
                
              </p>
            )}
          </div>
        </div>
      )}
    </div>
  );
}
